.logo {
  height: 30vmin;
  pointer-events: none;
}
.Header {
  padding-top: 20px;
  height: 100vh;
  position: relative;
}
.theme-zone {
  position: absolute;
  right: 20px;
}
.button {
  padding: 6px 10px;
  width: 60px;
  font-size: 16px;
  margin-left: 20px;
  transition: color 2s ease, background-color 2s ease;
}
.button-light {
  background-color: #ffffff;
  color: rgb(85, 228, 180);
  border: 2px solid rgb(85, 228, 180);
  border-radius: 2px;
}
.button-dark {
  background-color: #020520;
  color: rgb(85, 228, 180);
  border: 2px solid rgb(85, 228, 180);
  border-radius: 2px;
}
.App-header {
  min-height: calc(100vh - 54px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  transition: color 2s ease, background-color 2s ease;
}

.App-header-light {
  background-color: #ffffff;
  color: rgba(2, 5, 40, 0.85);
}
.App-header-dark {
  background-color: #020520;
  color: rgba(255, 255, 255, 0.85);
}

.link-github {
  text-decoration: none;
  transition: color 2s ease, background-color 2s ease;
}
.link-github-light {
  color: rgba(31, 39, 31, 0.6);
}

.link-github-dark {
  color: rgba(255, 255, 255, 0.6);
}

.link-github-light:active {
  color: #00ff73;
}
.link-github-dark:active {
  color: #5ffab9;
}
.link-github-light:visited {
  color: rgba(31, 39, 31, 0.6);
}

.link-github-dark:visited {
  color: rgba(255, 255, 255, 0.6);
}

@keyframes glow-light {
  0% {
    color: rgba(31, 39, 31, 0.6);
  }
  50% {
    color: rgb(255, 135, 40);
  }
  100% {
    color: rgba(31, 39, 31, 0.6);
  }
}
@keyframes glow-dark {
  0% {
    color: rgba(255, 255, 255, 0.6);
  }
  50% {
    color: rgb(255, 210, 125);
  }
  100% {
    color: rgba(255, 255, 255, 0.6);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .glow-light {
    animation: glow-light infinite 5s linear;
  }
  .glow-dark {
    animation: glow-dark infinite 5s linear;
  }
}
